.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.section {

  width: 100%;

  &__title {
    background-color: $softRed;
    padding: 15px;
    color: white;
    font-weight: normal;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 20px;
  }
}